<template>
    <div>
        <button class="btn primary" @click="modal=true">預約時段</button>
        <Modal
            v-model="modal"
            :footer-hide="true"
            :closable="false"
            :mask-closable="false"
            class-name="vertical-center-modal"
            width="423"
            @on-visible-change="showModal"
        >
            <p class="title">預約時段</p>
            <Form ref="form" :model="formValidate" :rules="ruleValidate">
                <FormItem prop="name" label="姓名">
                    <Input v-model="formValidate.name"></Input>
                </FormItem>
                <FormItem prop="tel" label="電話">
                    <Input v-model="formValidate.tel"></Input>
                </FormItem>
                <FormItem label="預約起迄">
                    <Row>
                        <Col span="8">
                            <FormItem prop="startDate">
                                <DatePicker
                                    type="date"
                                    placeholder="Select date"
                                    v-model="formValidate.startDate"
                                ></DatePicker>
                            </FormItem>
                        </Col>
                        <Col span="2" style="text-align: center">-</Col>
                        <Col span="8">
                            <FormItem prop="endDate">
                                <DatePicker
                                    type="date"
                                    placeholder="Select date"
                                    v-model="formValidate.endDate"
                                ></DatePicker>
                            </FormItem>
                        </Col>
                    </Row>
                </FormItem>
                <Row>
                    <dl class="session">
                        <dt>平日時段</dt>
                        <dd>{{total.normalDay}}夜</dd>
                        <dt>假日時段</dt>
                        <dd>{{total.holiday}}夜</dd>
                    </dl>
                    <p class="totalPrice">= NT.{{total.totalPrice}}</p>
                </Row>
                <FormItem>
                    <button class="btn" style="margin-right:8px" @click.prevent="modal=false">取消</button>
                    <button class="btn primary reserve" :class="{ active: loading }" @click.prevent="submitHandler">確定預約</button>
                </FormItem>
            </Form>
        </Modal>
    </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
    data() {
        return {
            modal: false,
            loading: false,
            formValidate: {
                name: '',
                tel: '',
                startDate: '',
                endDate: ''
            },
            ruleValidate: {
                name: [{ required: true, message: '必填' }],
                tel: [{ required: true, message: '必填' }],
                startDate: [{ required: true, message: '必填' }],
                endDate: [{ required: true, message: '必填' }]
            },
            total: {},
        }
    },
    computed: {
        reserveDate() {
            return this.$store.state.reserveDate;
        },
        room() {
            return this.$store.state.room;
        },
        price() {
            let { normalDayPrice, holidayPrice } = this.room;
            return { normalDayPrice, holidayPrice };
        }
    },
    watch: {
        'formValidate.startDate'() {
            this.total = this.getTotal();
        },
        'formValidate.endDate'() {
            this.total = this.getTotal();
        },
    },
    methods: {
        showModal(visible) {
            if (!visible) return;
            let defaultDay = moment().add(1, 'days');
            this.formValidate.startDate = new Date(this.reserveDate[0] || defaultDay);
            this.formValidate.endDate = new Date(this.reserveDate[this.reserveDate.length - 1] || defaultDay);
        },
        submitHandler(name) {
            this.$refs.form.validate(valid => {
                if (!valid) return;
                let dates = this.getAllDates();
                this.submit(dates)
                    .then(res => {
                        if (res.data.success) {
                            this.$store.commit('updateReserveResult', '');
                            this.$store.commit('updateReserveDate', dates);
                            this.$store.dispatch('getRoom', this.$route.params.id);
                        }
                        else if (res.status !== 200) {
                            this.$store.commit('updateReserveResult', res.data.message);
                        }
                    })
                    .catch(err => {
                        console.log(err);
                        this.$store.commit('updateReserveResult', '發生錯誤，請稍後再試。');
                    })
                    .finally(() => {
                        this.loading = false;
                        this.modal = false;

                        // test
                        // this.$store.commit('updateReserveResult', '您所提供的訂房時間(date)已有訂房。');
                        // this.$store.commit('updateReserveResult', '');
                        // this.$store.commit('updateReserveDate', dates);
                        // this.$store.dispatch('getRoom', this.$route.params.id);
                    });
            });
        },
        submit(dates) {
            this.loading = true;
            return axios.post(`https://challenge.thef2e.com/api/thef2e2019/stage6/room/${this.$route.params.id}`, {
                headers: {
                    'Authorization': 'Bearer 4jGwjfznoX5ftKlftGTPRrm8DSTpZAzILdyoxCvtTZGQBSbSJzsEN0ldgOg7',
                    'accept': 'application/json'
                },
                data: {
                    name: this.name,
                    tel: this.tel,
                    date: dates
                },
            });
        },
        getAllDates() {
            // 因原時間格式在getAllDates函式中有bug，故此處轉了兩次格式以避開此問題
            let startDate = new Date(moment(this.formValidate.startDate).format('YYYY-MM-DD'));
            let endDate = new Date(moment(this.formValidate.endDate).format('YYYY-MM-DD'));

            let dateArray = [];
            let currentDate = moment(startDate);
            let stopDate = moment(endDate);
            while (currentDate <= stopDate) {
                dateArray.push(currentDate.format('YYYY-MM-DD'))
                currentDate = currentDate.add(1, 'days');
            }
            return dateArray;
        },
        getTotal() {
            // 因原時間格式在getAllDates函式中有bug，故此處轉了兩次格式以避開此問題
            let startDate = new Date(moment(this.formValidate.startDate).format('YYYY-MM-DD'));
            let endDate = new Date(moment(this.formValidate.endDate).format('YYYY-MM-DD'));

            let normalDay = 0;
            let holiday = 0;
            let totalPrice = 0;
            let currentDate = moment(startDate);
            let stopDate = moment(endDate);
            while (currentDate <= stopDate) {
                let day = currentDate.day();
                if (day === 0 || day === 6) {
                    holiday++;
                    totalPrice += this.price.holidayPrice;
                }
                else {
                    normalDay++;
                    totalPrice += this.price.normalDayPrice;
                }
                currentDate = currentDate.add(1, 'days');
            }
            return { totalPrice, normalDay, holiday };
        }
    },
}
</script>

