    }
    .session {
        display: flex;
        width: 120px;
        flex-wrap: wrap;
        dt,
        dd {
            width: 50%;
        }
    }
    .totalPrice {
        padding-top: 24px;
        padding-bottom: 24px;
        color: #ff5c5c;
        font-size: 26px;
    }
}
.btn.reserve.active {
    opacity: .5;
    pointer-events: none;
    &::after {
        content: '...';
    }
}
</style>